.login {
    label {
        padding-bottom: 1%;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10% 0;
    .login__username, .login__password {
        padding: .5% 0;
        input {
            width: 15rem;
            height: 2rem;
        }
    }
    button {
        width: 10rem;
        height: 2rem;
    }
}

